@use './components/gerillass' as g;
@use './helpers' as *;
@use './components/spinner' as *;
@use './components/progress' as *;
@use 'components/dialog' as *;

@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

@include g.reset-css;

@include simple-reset {
    &::after {
        content: '';

        position: fixed;

        @include sizer(50%);
        // height: 50vh;
        // width: 50vw;
        top: 25vh;
        left: 25vw;
        margin: auto;
        z-index: -5;
        background: url('./pictures/moviequiz.webp') no-repeat center center;
        @include webp(false) {
            background: url('./pictures/moviequiz.png') no-repeat center center;
        }
        background-size: auto;
    }
}

@include spinner;
@include progress;
@include dialog;
@include progress-line;
